
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Muli', sans-serif;
  -moz-osx-font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  text-shadow: rgba(0, 0, 0, .01) 0 0 1px;
}

body {
  margin: 0;
  background-color: white;
}

.main {
  width: inherit;
  height: inherit;
}

.cover {
  font-size: large;
  background-color: #DCE7F2;
  color: #0D2C5B;
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  opacity: 1;
  background-image: url('./img/raylight.jpg');
  background-size: cover;
  margin: 0;
  padding: 0;
  border-radius: 1px;
  margin-bottom: 32px;
}

.header-title-container, .section-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}

.section-row {
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 250px;
  padding-right: 250px;
  background-color: rgba(0, 0, 0, 0.06);
  margin-bottom: 32px;
}

.footer-row {
  font-family: 'Roboto Mono', monospace;
  font-weight: 600;
  color: #FFFFFF;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 150px;
  padding-right: 150px;
  background-color: #0D2C5B;
  text-align: center;
}

.section-title {
  color: #0D2C5B;
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 18px;
}

.section-text {
  color: #0D2C5B;
  font-weight: 500;
  font-size: 20px;
}

.page-title {
  color: #0D2C5B;
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 18px;
}


ul {
  text-align: left;
}

li, p {
  color: black;
  margin-bottom: 3px;
  font-size: 20px;
}

.wrapper {
  max-width: 1024px;
  display: grid;
  grid-column-gap: 24px;
  grid-row-gap: 24px;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fill, minmax(365px, 1fr));
  grid-auto-rows: minmax(300px, auto);
  padding-top: 32px;
  padding-bottom: 32px;
}

.wrapper > * {
  color: #FFFFFF;
  border-radius: 1px;
  padding: 20px;
}

img {
  position: relative;
  -ms-flex-align: center;
      align-items: center;
}

h1 {
  font-size: 60px;
  font-weight: 900;
  margin: 10px;
  padding: 0;
  color: white;
}

h2 {
  font-size: 35px;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
}

h4 {
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
  margin: 24px;
  color: white;
  text-shadow: 1px 1px #5E82A3;
}

.btn {
  font-family: 'Muli', sans-serif;
  overflow-y: hidden;
  background-color: transparent;
  border: 3px solid #DCE7F2;
  border-radius: 16px;
  color: #DCE7F2;
  font-size: 1rem;
  font-weight: 700;
  padding: 12px;
  cursor: pointer;
}

.btn:hover {
  border: 3px solid #0D2C5B;
  color: #0D2C5B;
}

.email{
  color: black;
}

.email:hover{
  color: #0D2C5B;
}


.panel1 {
  background-color: #DC7012;
  background-image: url('./img/landscape.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.panel2 {
  background-color: #D3D5D4;
  color: #000000;
  background-image: url('./img/lightray.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.panel3 {
  background-color: #C72230 ;
  background-image: url('./img/announce.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.panel4 {
  background-color: #167C80;
  background-image: url('./img/events.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.panel5 {
  background-color: #0D2C5B;
  /*background-color: #1978D1;*/
  background-image: url('./img/commty.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

@media (max-width: 480px), (min-width: 480px) and (max-width: 768px) {
  h1 {
    font-size: 30px;
    font-weight: 900;
    margin: 0;
  }

  h2 {
    font-size: 20px;
    font-weight: 700;
  }

  h4 {
    font-size: 16px;
  }

  .cover {
    background-image: url('./img/raylight_small.jpg');
    background-size: cover;
  }

  .section-row {
    padding-top: 60px;
    padding-bottom: 60px;
    padding-right: 30px;
    padding-left: 30px;
  }

  .section-title {
    font-size: 20px;
    font-weight: 700;
  }

  .section-text {
    font-size: 15px;
    font-weight: 400;
  }

  .page-title {
    font-size: 30px;
    font-weight: 700;
  }

  .footer-row {
    font-size: 12px;
    padding-left: 50px;
    padding-right: 50px;
  }
}

@media (min-width: 768px) and (max-width: 1224px) {
  h1 {
    font-size: 60px;
  }

  h2 {
    font-size: 30px;
  }

  h4 {
    font-size: 24px;
  }
  .section-row {
    padding-top: 80px;
    padding-bottom: 80px;
    padding-right: 50px;
    padding-left: 50px;
  }

  .section-title {
    font-size: 30px;
  }

  .section-text {
    font-size: 20px;
  }

  .page-title {
    font-size: 40px;
    font-weight: 700;
  }
}

@media  (min-width: 1224px) {
  h1 {
    font-size: 100px;
  }

  h4 {
    font-size: 30px;
  }
}

/* We need to set the margin used on flex items to 0 as we have gaps in grid.  */

@supports (display: grid) {
  .wrapper > * {
    margin: 0;
  }
}

.box-text-area {
  opacity: 0.6;
  background-color: white;
  font-weight: 500;
  /*background-color: #DCE7F2;*/
  padding: 12px;
  border-radius: 3px;
  margin-bottom: 24px;
  color: white;
}

.box-text {
  opacity: 1;
  color: black;
  font-size: 20px;
}




